import React from 'react';
import classNames from 'classnames';
import Modal from './Modal';
import { Button, ButtonGroup } from 'ship-components-buttons';
import css from './Dialog.css';
import invariant from 'invariant';

export default class Dialog extends React.Component {

  constructor(props) {
    super(props);

    this.handleCloseModal = this.handleCloseModal.bind(this);
  }

  handleCloseModal() {
    this.props.onClose();
  }

  getDefaultButtons() {
    return [{
      label: 'OK',
      onClick: this.handleCloseModal
    }];
  }

  getButtons() {
    return this.props.buttons || this.getDefaultButton();
  }

  render() {
    return (
      <Modal
        {...this.props}
        className={classNames(css.container, this.props.className)}
        header={false}
      >
        {this.props.name ?
          <h1 className={classNames(css.title)}>{this.props.name}</h1>
        : null}
        <div className={classNames(css.body)}>
          {this.props.message || this.props.children}
        </div>
        <ButtonGroup
          align='right'
          className={classNames(css.controls)}
        >
          {this.getButtons().map((btn) => {
            return (
              <Button
                key={btn.label}
                disabled={btn.disabled === true}
                onClick={btn.onClick} >
                  {btn.label}
              </Button>
            );
          })}
        </ButtonGroup>
      </Modal>
    );
  }
}

Dialog.defaultProps = {
  confirm: 'Yes',
  cancel: 'Cancel',
  onConfirm: function() {
    invariant(
      false,
      'Dialog.props.onConfirm is not defined'
    );
  },
  onCancel: function() {
    invariant(
      false,
      'Dialog.props.onCancel is not defined'
    );
  },
  disableConfirm: false
};



